* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;

  ::-webkit-scrollbar,
  ::-webkit-scrollbar-thumb,
  ::-webkit-scrollbar-track {
    width: 3.9px;
    border: none;
    background: #333;
  }

  ::-webkit-scrollbar-button,
  ::-webkit-scrollbar-track-piece,
  ::-webkit-scrollbar-corner,
  ::-webkit-resizer {
    display: none;
  }

  ::-webkit-scrollbar-thumb {
    width: 100%;
    border-radius: 5px;
    background-color: rgb(195, 160, 36);
  }
}

body {
  min-height: 100vh;
  overflow: auto;
  overflow-x: hidden;
  background: #000;
}

a {
  text-decoration: none;
}

.App {
  height: 100vh;
  width: 100%;
}

@import '_intro.scss';