.IntroPage {
  background: rgb(39, 39, 39);
  height: 100vh;
}

.Intro {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
  .title {
    font-size: 79px;
    font-family: 'Yomogi', cursive;
    font-weight: 900;
    color: wheat;
    width: 15.3rem;
    margin-left: -89px;
    margin-top: -69px;
    
    transition: all 0.5s ease-in-out;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    span {
      opacity: 0;
      margin-top: 0px;
    }
    #s {
      animation:
      drop 0.5s forwards linear,
      wavi 0.8s 0.5s infinite linear;
    }
    #e {
      animation:
      drop 0.5s 0.3s forwards linear,
      wavi 0.8s 0.8s infinite linear;
    }
    #h {
      animation:
      drop 0.5s 0.6s forwards linear,
      wavi 0.8s 1.1s infinite linear;
    }
    #m {
      animation:
      drop 0.5s 0.9s forwards linear,
      wavi 0.8s 1.4s infinite linear;
    }
    #a {
      animation:
      drop 0.5s 1.2s forwards linear,
      wavi 0.8s 1.7s infinite linear;
    }
    #t {
      animation:
      drop 0.5s 1.5s forwards linear,
      wavi 0.8s 2s infinite linear;
    }
  }
}

@keyframes drop {
  0% {
    opacity: 0;
    margin-top: -100px;
  }
  100% {
    opacity: 1;
    margin-top: 0px;
  }
}

@keyframes wavi {
  0%, 100% {
    margin-top: 0px;
  }
  25% {
    margin-top: 1.3px;
  }
  75% {
    margin-top: -1.3px;
  }
}

#container {
  position: absolute;
  top: 31%;
  left: 43%;
  transition: all 0.5s ease-in-out;
  svg {
    transform: rotate(-1.3deg);
  }
}

.slogan {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  font-size: 33px;
  width: 79%;
  color: bisque;
  transition: all 0.69s ease-out;
  transform: translate(-50%, -50%);
  margin-top: 100px;
  opacity: 0;
  font-family: 'Encode Sans SC', sans-serif;
}

.transition1, .transition2, .transition3,.transition4,.transition5, .transition6 {
  position: absolute;
  top: 0%;
  left: 0%;
  right: 100%;
  bottom: 0%;
  transform: rotate(0deg);
}

.transition1 {
  background: #750787;
  animation: wipe 1s 7s linear;
}
.transition2 {
  background: #004dff;
  animation: wipe 1s 7.13s linear;
}
.transition3 {
  background: #008026;
  animation: wipe 1s 7.26s linear;
}
.transition4 {
  background: #ffed00;
  animation: wipe 1s 7.39s linear;
}
.transition5 {
  background: #ff8c00;
  animation: wipe 0.9s 7.52s linear;
}
.transition6 {
  background: #e40303;
  // width: 90vh;
  // left: -90vh;
  // animation: wipeLast 0.8s 7.8s linear;
  animation: wipe 0.8s 7.8s linear;
}

@keyframes wipe {
  0% {
    right: 100%;
    left: 0%;
  }
  50% {
    right: 0%;
    left: 0%;
  }
  100% {
    right: 0%;
    left: 100%;
  }
}

